  width: 200px;
  height: 20px;

  opacity: .01;
}

.fileWrapper {
  .textWrapper {
    font-size: .9em;

    color: #aaa;
  }
  a {
    font-size: .9em;

    color: #205ea4;
  }
}

div.file {
  padding: 0 0 4px 0;
  .icon {
    position: relative;
    top: 3px;

    display: inline-block;

    margin-right: 3px;
  }
}

.icon,
#fileUploadWrapper {
  background-color: transparent;
  background-image: url('../images/mac/icons.png?v=2?version=3.1.570');
  background-repeat: no-repeat;
}

.clip {
  width: 10px;
  height: 12px;

  background-position: -17px -36px;
}

#fileUploadWrapper {
  width: 22px;
  height: 20px;

  background-position: 0 -51px;
}

.msg {
  .image {
    width: 16px;
    height: 16px;

    background-position: -23px -53px;
  }
  .pdf {
    width: 16px;
    height: 16px;

    background-position: 0 -17px;
  }
  .txt {
    width: 16px;
    height: 16px;

    background-position: 0 -34px;
  }
  .unknown {
    width: 16px;
    height: 16px;

    background-position: 0 0;
  }
  .word {
    width: 16px;
    height: 16px;

    background-position: -28px -36px;
  }
}

#fileUploadWrapper .text {
  font-size: 11px;
  span {
    text-decoration: underline;

    color: #467cb3;
  }
}

.progressBarWrapper {
  width: 90%;
  margin: 0 auto .667em;
  .progressBar {
    margin-top: 3px;
    padding: 2px;

    border: 1px solid #c1c1c1;
    border-radius: 6px;
    background: #d5d5d5;
    background-image:         linear-gradient(top, #d5d5d5 0, #e4e4e4 100%);
    background-image:      -o-linear-gradient(top, #d5d5d5 0, #e4e4e4 100%);
    background-image:    -moz-linear-gradient(top, #d5d5d5 0, #e4e4e4 100%);
    background-image: -webkit-linear-gradient(top, #d5d5d5 0, #e4e4e4 100%);
    background-image:     -ms-linear-gradient(top, #d5d5d5 0, #e4e4e4 100%);
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #d5d5d5), color-stop(1, #e4e4e4));
    .progress {
      height: 4px;

      border: 1px solid #6ca3cb;
      border-radius: 3px;
      background: #98d7fb;
      background-image:         linear-gradient(top, #98d7fb 0, #81c0f6 100%);
      background-image:      -o-linear-gradient(top, #98d7fb 0, #81c0f6 100%);
      background-image:    -moz-linear-gradient(top, #98d7fb 0, #81c0f6 100%);
      background-image: -webkit-linear-gradient(top, #98d7fb 0, #81c0f6 100%);
      background-image:     -ms-linear-gradient(top, #98d7fb 0, #81c0f6 100%);
      background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #98d7fb), color-stop(1, #81c0f6));
    }
  }
  .legend,
  .percentage {
    color: #aaa;
  }
}

#chatItself a {
  color: #205ea4;
}

#departmentsWrapper {
  position: absolute;

  display: none;

  border: 1px solid #b3b3b3;
  background: #fff;
  box-shadow: inset 0 1px 2px #ddd;
}

.departmentsShow #departmentsWrapper {
  top: 8.9em;
  right: 1.14em;
  bottom: 1.357em;
  left: 1.14em;
}

.departmentsForce #departmentsWrapper {
  top: 7.483em;
}

.departmentsShow {
  #chooseDepartment {
    display: block !important;
  }
  #departmentsWrapper {
    display: block;
  }
}

#departments {
  margin: 0;
  padding: 0;

  list-style: none;
  li {
    position: relative;

    padding: 8px 8px 8px 23px;

    cursor: pointer;

    border-bottom: 1px solid #b3b3b3;
    &:hover {
      background: #e9f6f9;
    }
    div {
      &.name {
        font-size: 1.167em;
      }
      &.description {
        font-size: 1em;

        min-height: 1.167em;

        color: #7f7f7f;
      }
    }
    span {
      position: absolute;
      top: 11px;
      left: 10px;

      width: 8px;
      height: 8px;
    }
    &.online span {
      background: url('../images/mac/departments.png?v=2?version=3.1.570') no-repeat;
    }
    &.offline span {
      background: url('../images/mac/departments.png?v=2?version=3.1.570') no-repeat 0 -8px;
    }
  }
}

.departments .showDepartments {
  display: inline;
}

.showDepartments {
  display: none;

  cursor: pointer;
  .icon {
    position: relative;

    display: inline-block;

    width: 15px;
    height: 15px;

    border: 1px solid #cdcdcd;
    border-radius: 2px;
    background: #fff;
  }
}

.hideDepartments .icon {
  position: relative;

  display: inline-block;

  width: 15px;
  height: 15px;

  border: 1px solid #cdcdcd;
  border-radius: 2px;
  background: #fff;
}

.showDepartments .icon span {
  position: relative;
  z-index: 3;

  display: block;

  width: 11px;
  height: 9px;
  margin: 3px auto;

  background: url('../images/mac/departments.png?v=2?version=3.1.570') no-repeat 0 -24px;
}

.hideDepartments {
  cursor: pointer;
  .icon span {
    position: relative;
    z-index: 3;

    display: block;

    width: 11px;
    height: 10px;
    margin: 4px auto;

    background: url('../images/mac/departments.png?v=2?version=3.1.570') no-repeat 0 -33px;
  }
}

.showDepartments .txt,
.hideDepartments .txt {
  position: relative;
  bottom: 4px;

  margin-left: .333em;

  border-bottom: 1px dashed #000;
}

.showDepartments:hover .txt,
.hideDepartments:hover .txt {
  color: #467bc2;
  border-bottom-color: #467bc2;
}

.departmentsForce {
  .hideDepartments,
  #chatInput {
    display: none;
  }
  #online .center {
    padding-bottom: 0;
  }
  #onlinePanel {
    bottom: 9em;
  }
}

#chooseDepartment .topPanel {
  margin: .417em 0;
}

.chooseDepartmentTextWrapper {
  position: absolute;
  top: 5em;
  right: 1.25em;
  left: 1.25em;
}

#stickyMessage {
  position: relative;

  overflow: hidden;

  margin: 0 .8em 0;
  padding: .8em;

  border: 1px solid #b3b3b3;
  border-radius: 3px;
  background: #e8f4fe;
  input {
    line-height: 1.286em;

    width: 100%;
    margin: 0;

    text-indent: 5px;

    border: 1px solid #aaa;
    -moz-border-radius: .167em;
         border-radius: .167em;
    box-shadow: 0 1px 2px #ccc inset;
  }
}

#leaveContactsText {
  margin-bottom: .4em;
}

.leaveContactsInput {
  margin: .2em .3em;

  text-align: center;
  .placeholder {
    color: #aaa;
  }
}

.leaveContactsError {
  font-size: .9em;

  text-align: center;
}

#leaveContactsSubmit {
  font-family: Arial,Verdana,Sans-serif;

  display: inline-block;

  margin: .2em auto 0;
  padding: .4em 1em .4em;

  cursor: pointer;

  color: #fff;
  border: none;
  border-radius: 3px;
  background: #bb2626;
  &:focus,
  &:hover {
    background: #cc3434;
  }
}

#stickyMessageClose {
  position: absolute;
  top: .6em;
  right: .6em;

  width: .583em;
  height: .583em;

  cursor: pointer;

  background: url('../images/mac/close.png?v=2?version=3.1.570');
  background-size: .583em;
}

.colorifier {
  opacity: .1;
}
